<template>
  <div style="padding:0 20px;">
    <div class="logintitle">
      <img src="../../image/logo.jpg" style="width:100px;height:100px;" alt />
      <!-- <div>商机管理</div> -->
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="username"
        name="手机号码"
        label="手机号码:"
        placeholder="手机号码"
        :rules="[{ required: true, message: '请填写手机号码' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码:"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin-top: 36px;padding:0 20px;">
        <van-button round block type="info" native-type="submit">登录</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { Form, Field, Icon, Button, Toast } from "vant";

export default {
  components: {
    [Form.name]: Form,
    [Icon.name]: Icon,
    [Field.name]: Field,
    [Button.name]: Button,
  },
  data() {
    return {
      username: "",
      password: "",
    };
  },
  created() {
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    onSubmit(values) {
		let that = this;
      // eslint-disable-next-line no-console
      console.log("submit");
      this.$http
        .post("/admin/login/index", {
          account: this.username,
          password: this.password,
        })
        .then(function (response) {
          // eslint-disable-next-line no-console
          console.log(response.data);

          if (response.data.code === 2000) {
            sessionStorage.setItem("token", response.data.data.token);
            that.$router.push("/");
          }
        })
        // eslint-disable-next-line no-unused-vars
        .catch(function (error) {
          // eslint-disable-next-line no-mixed-spaces-and-tabs
          Toast("登录失败");
        });
    },
  },
};
</script>

<style>
body {
  background: white;
}
.logintitle {
  padding-top: 120px;
  padding-bottom: 10px;
  color: #409eff;
  text-align: center;
}
</style>
